import { setToken, removeToken } from '@/util/auth'

import { getInfo, login } from '@/api/Login'

import { resetRouter } from '@/router/index'

const state = {
	token: '',
	name: '',
	clazz: '',
	roles: [],
	avatar:''
}

const mutations = {
	SET_TOKEN: (state, token) => {
		state.token = token
	},
	SET_NAME: (state, name) => {
		state.name = name
	},
	SET_ROLES: (state, roles) => {
		state.roles = roles
	},
	SET_CLAZZ: (state, clazz) => {
		state.clazz = clazz
	},
	SET_AVATAR:(state,avatar)=>{
		state.avatar = avatar
	}
}

const actions = {
	login({ commit }, userInfo) {
		return new Promise((resolve, reject) => {
			login(userInfo).then(response => {
				const token = response.data.obj
				commit('SET_TOKEN', token)
				setToken(token)
				resolve()
			}).catch(error => {
				reject(error)
			})
		})
	},

	getInfo({ commit }, token) {
		return new Promise((resolve, reject) => {
			getInfo({ token: token }).then(response => {
				const { data } = response
				if (!data) {
					reject("verification fail")
				}
				const { name, roles, clazzId } = data
				if (!roles || roles.length < 0) {
					reject("roles must be a non-null array")
				}
				commit('SET_NAME', name)
				commit('SET_ROLES', roles)
				commit('SET_CLAZZ', clazzId)
				resolve(data)
			}).catch(error => {
				reject(error)

			})
		})
	},

	logout({ commit }) {
		removeToken()
		commit('SET_TOKEN', '')
		commit('SET_ROLES', [])
		commit('SET_NAME', '')
		commit('SET_CLAZZ', '')
		resetRouter()
	},

	setAvatar({commit},avatar){
		if(avatar!==null){
			commit('SET_AVATAR',avatar)
		}
	}


}

export default {
	namespaced: true,
	state,
	mutations,
	actions
}
