<template>
  <el-header>
    <el-button size="mini" @click="changeCollapse" class="sidebar-toggle">
      <font-awesome-icon :icon="['fas','bars']" size="lg">test</font-awesome-icon>
    </el-button>
    <!-- <span>{{ this.$route.meta.title }}</span> -->
    <el-dropdown @command="handleCommand" class="drop" trigger="click" :hide-on-click="true">
      <span class="el-dropdown-link">
        <el-avatar :src="avatar"></el-avatar>
        <i class="el-icon-caret-bottom"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="a">
          用户中心
        </el-dropdown-item>
        <el-dropdown-item command="b">
          退出登录
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </el-header>
</template>
<script>
import store from "@/store/index";
import sha1 from 'js-sha1'
export default {
  name: "pageHeader",
  data() {
    return {
      user: store.getters.name,
      isCollapse: store.getters.sidebar,
    };
  },
  computed:{
    avatar:function(){
      let ava = 'https://gravatar.com/avatar/'+sha1(this.user)+'?d=retro'
      this.$store.dispatch('user/setAvatar',ava)
      return ava
    }
  },
  methods: {
    logout() {
      this.$store.dispatch("user/logout");
      this.$router.push({ path: "/login" });
    },
    handleCommand(command){
      if(command==='b'){
        this.logout()
      }else if(command==='a'){
        this.$router.push('/manage/center')
      }
    },
    changeCollapse() {
      if (store.getters.sidebar === true) {
        this.$store.dispatch("sidebar/closeSidebar");
      } else {
        this.$store.dispatch("sidebar/openSidebar");
      }
    },
   setAvatar(){
     if(this.avatar ===''){
       let ava = 'https://gravatar.com/avatar/'+sha1(this.user)+'?d=retro'
       this.$store.dispatch('user/setAvatar',ava)
     }
   }
  }
};
</script>
